.canvas {
  aspect-ratio: 1 / 1;
  background-color: #fff;
  margin: auto;
  padding: 0;
  position: absolute;
  inset: 0;
}

.controls {
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: auto;
  display: flex;
  position: absolute;
  top: 20px;
  bottom: 20px;
  left: 10px;
}

.formdata {
  border: 1px solid #000;
  border-radius: 5px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: 20px;
  padding: 5px;
  display: flex;
  overflow-y: auto;
}

.formdata textarea {
  width: 90%;
  margin-bottom: 20px;
}

.fb {
  justify-content: center;
  align-items: center;
  display: flex;
}

.fb-h {
  flex-direction: column;
}

@media (max-width: 1000px) {
  .canvas {
    display: none;
  }

  .btn {
    width: 90vw;
    height: 20vh;
    font-size: 30px;
  }

  .controls {
    left: 0;
    right: 0;
  }
}

